import Vue from 'vue';
import {
  Button,
  Input,
  Checkbox,
  Select,
  Radio,
} from 'ant-design-vue';

import App from './App.vue';
import router from './router';
import store from './store';

Vue.use(Button);
Vue.use(Input);
Vue.use(Checkbox);
Vue.use(Select);
Vue.use(Radio);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
